atui-sidebar {
    atui-sidebar-menu {
        atui-sidebar-submenu [slot='submenu-content'] {
            atui-sidebar-menuitem {
                padding-left: 36px;
            }

            atui-sidebar-submenu [slot='submenu-content'] {
                atui-sidebar-menuitem {
                    padding-left: 50px;
                }

                atui-sidebar-submenu [slot='submenu-content'] {
                    atui-sidebar-menuitem {
                        padding-left: 64px;
                    }
                }
            }
        }
    }

    atui-menu {
        [data-name='menu-content-wrapper'] {
            background-color: var(--token-sidebar-background);
        }
    }
}
