import {
    Component,
    Prop,
    State,
    Event,
    EventEmitter,
    h,
    Method,
    Host,
} from '@stencil/core';
import { cva } from 'class-variance-authority';

const variants = cva(
    'transition-size relative flex flex-col overflow-y-auto overflow-x-hidden bg-sidebar-background text-sidebar-foreground duration-300 ease-in-out',
    {
        variants: {
            collapsible: {
                none: null,
                icon: 'min-w-sidebar-collapsed group-data-[state=collapsed]/sidebar-wrapper:w-sidebar-collapsed',
                offcanvas: 'group-data-[state=collapsed]/sidebar-wrapper:w-0',
            },
            side: {
                left: null,
                right: 'order-1',
            },
        },
    },
);

export type Side = 'left' | 'right';
export type Collapsible = 'offcanvas' | 'icon' | 'none';
export type Width =
    | 'menu'
    | 'panel-xs'
    | 'panel-sm'
    | 'panel-md'
    | 'panel-lg'
    | 'panel-xl'
    | 'auto';

/**
 * @category Navigation
 * @description A collapsible sidebar navigation component with menu support and responsive behavior. Features animation, auto-collapse, and keyboard navigation.
 * @slot sidebar-header - Header of the sidebar (likely to be atui-sidebar-trigger)
 * @slot sidebar-content - Content displayed in the sidebar (likely to be atui-sidebar-menu elements)
 * @slot sidebar-footer - Footer displayed in the sidebar
 * @slot page-content - Content of the page
 */
@Component({
    tag: 'atui-sidebar',
    shadow: false,
    styleUrl: 'atui-sidebar.scss',
})
export class AtuiSidebarComponent {
    /**
     * Position of the sidebar on the page
     */
    @Prop() side: Side = 'left';

    /**
     * Width of the sidebar
     */
    @Prop() width: Width = 'menu';

    /**
     * Size of the sidebar when collapsed.
     */
    @Prop() collapsible: Collapsible = 'icon';

    /**
     * Opens the sidebar by default when set
     */
    @Prop() default_open?: boolean = true;

    @State() isOpen: boolean = false;

    /**
     * Emits an even when the sidebar is toggled, with `event.detail` being true if the sidebar is now open
     */
    @Event()
    atuiSidebarChange: EventEmitter;

    componentWillLoad() {
        if (this.default_open !== undefined) {
            this.isOpen = this.default_open;
        }
        this.atuiSidebarChange.emit(this.isOpen);
    }

    /**
     * Toggles the sidebar's open state.
     */
    @Method()
    async toggleSidebar(): Promise<void> {
        this.isOpen = !this.isOpen;
        console.log('stenciltoggleSidebar', this.isOpen);
        this.atuiSidebarChange.emit(this.isOpen);
    }

    /**
     * Getter method for the open state of the sidebar
     * @returns The current open state of the sidebar
     */
    @Method()
    async getIsOpen(): Promise<boolean> {
        return this.isOpen;
    }

    render() {
        const classname = variants({
            collapsible: this.collapsible,
            side: this.side,
        });
        return (
            <Host
                data-state={this.isOpen ? 'expanded' : 'collapsed'}
                data-collasable={this.collapsible}
                data-side={this.side}
                class="group/sidebar-wrapper flex h-screen w-full items-stretch overflow-y-auto overflow-x-hidden"
            >
                <nav
                    data-name="sidebar-nav"
                    class={`w-${this.width} ` + classname}
                >
                    <div class="flex p-8">
                        <slot name="sidebar-header" />
                    </div>

                    <div class="align-items flex flex-1 flex-col p-8">
                        <slot name="sidebar-content" />
                    </div>

                    <div class={`${this.isOpen ? '' : 'hidden'}`}>
                        <slot name="sidebar-footer"></slot>
                    </div>
                </nav>
                <div class="flex w-full overflow-auto">
                    <slot name="page-content" />
                </div>
            </Host>
        );
    }
}
